.header {
  padding: 20px 0;
  background: #ffffff;
  box-shadow: 0px 4px 40px #f3f3f3;
  transition: 0.3s;
  // position: fixed;
  // left: 0;
  // top: 0;
  // width: 100%;
  // z-index: 2;

  .header-content {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &__left-menu {
      display: flex;
      align-items: center;

      &_nav {
        margin-left: 10px;

        .item {
          margin-left: 50px;
          font-size: 16px;
          line-height: 19px;
          font-weight: 400;
          color: $color-brown;
        }
      }
    }

    &__right-menu {
      display: flex;
      align-items: center;

      .language {
        position: relative;
        z-index: 2;

        &__items {
          display: none;
          list-style: none;
          position: absolute;
          top: 35px;
          left: 0;
          width: 100%;
        }

        &__items_active {
          opacity: 1;
          display: block;
        }

        &__item {
          opacity: 1;
          height: auto;
          padding: 10px;
          background-color: transparent;
          border-radius: 10px;
          cursor: pointer;
          font-size: 14px;
          color: #000000;
          font-weight: 700;
          display: flex;
          align-items: center;
          justify-content: center;

          a {
            display: block;
            width: 100%;
            color: #000;
          }
        }

        &__item_active {
          background-color: transparent;
          display: flex;
          align-items: center;
        }
      }

      .dark-mode {
        margin-left: 30px;
        .switch {
          position: relative;
          display: inline-block;
          width: 55px;
          height: 31px;
          border-radius: 100px;
        }

        /* Hide default HTML checkbox */
        .switch input {
          opacity: 0;
          width: 0;
          height: 0;
        }

        /* The slider */
        .slider {
          position: absolute;
          cursor: pointer;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background-color: #ccc;
          -webkit-transition: 0.4s;
          transition: 0.4s;

          .moon,
          .sun {
            top: 50%;
            transform: translateY(-50%);
            position: absolute;
          }

          .moon {
            right: 5px;
          }

          .sun {
            left: 5px;
          }
        }

        .slider:before {
          position: absolute;
          content: "";
          height: 21px;
          z-index: 2;
          width: 21px;
          left: 5px;
          bottom: 4px;
          top: 0;
          bottom: 0;
          margin: auto 0;
          -webkit-transition: 0.4s;
          transition: 0.4s;
          background: #ffffff;
          border: 0.5px solid rgba(0, 0, 0, 0.04);
          box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.15),
            0px 3px 1px rgba(0, 0, 0, 0.06);
        }

        input:checked + .slider {
          background-color: #444444;
        }

        input:focus + .slider {
          box-shadow: 0 0 1px #444444;
        }

        input:checked + .slider:before {
          transform: translateX(22px);
          background: #cecece;
          border: 0.5px solid rgba(0, 0, 0, 0.04);
          box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.15),
            0px 3px 1px rgba(0, 0, 0, 0.06);
        }

        /* Rounded sliders */
        .slider.round {
          border-radius: 34px;
        }

        .slider.round:before {
          border-radius: 50%;
        }
      }

      .button-position {
        margin-left: 40px;

        .btn {
          color: $color-brown;
          font-size: 16px;
          line-height: 19px;
          font-weight: 400;
        }

        .btn:last-of-type {
          margin-left: 30px;
        }
      }
    }
  }
}

body.dark .header {
  background: $color-dark;
  box-shadow: none;

  .header-content {
    &__left-menu {
      &_logo {
        a {
          svg {
            .path {
              fill: #d8d8d8;
            }
          }
        }
      }
    }
  }

  .item {
    color: $color-light-green;
  }

  .button-position .btn {
    color: $color-light-green;
  }
}
