* {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  text-decoration: none;
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

html,
body {
  margin: 0;
  font-family: "Rubik", sans-serif;
  overflow-x: hidden;
}

body.dark {
  background: #232323;
}

body.active {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.container {
  width: 1280px;
  margin: 0 auto;
}

.btn-green-border {
  color: $color-brown;
  font-size: 16px;
  line-height: 19px;
  font-weight: 400;
  border: 1px solid $color-green;
  border-radius: 10px;
  padding: 15px 22px;
  cursor: pointer;
  background: transparent;
}

.btn-green-text {
  display: flex;
  align-items: center;
  font-size: 18px;
  line-height: 28px;
  color: $color-green;
  font-weight: 500;

  .arrow {
    margin-left: 12px;
  }
}

body.dark .btn-green-text {
  display: flex;
  align-items: center;
  font-size: 18px;
  line-height: 28px;
  color: $color-green;
  font-weight: 500;

  .arrow {
    margin-left: 12px;

    svg {
      path {
        fill: #fff;
      }
    }
  }
}

.title {
  color: $color-brown;
  font-size: 46px;
  line-height: 55px;
  font-weight: 400;
}

body.dark .title {
  color: #fff;
}

.button-to-top {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  display: none;
  position: fixed;
  bottom: 25px;
  right: 10px;
  height: 40px;
  width: 49px;
  height: 49px;
  z-index: 99;
  background: #ffffff;
  border: 1px solid #000000;
  border-radius: 40px;
  outline: none;
  cursor: pointer;
  padding: 12px 6px 15px;
  transition: 0.3s;
  text-align: center;
  font-size: 20px;
  color: #fff;
  line-height: 1em;
  transition: 0.3s;

  .text {
    font-size: 10px;
    line-height: 12px;
    font-weight: 400;
    color: $color-brown;
    margin-top: 3px;
  }
}

body.dark .button-to-top {
  background: #2b2b2b;
  border: 1px solid #000000;

  .text {
    color: #ffffff;
  }
}
