.main {
  margin-top: 110px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: 0.3s;

  .back-green {
    position: absolute;
    right: 0;
    top: 89px;
    z-index: 1;
  }

  .map {
    position: absolute;
    right: 0;
    z-index: 2;
    mix-blend-mode: color-dodge;
    opacity: 1;
  }

  .filter-blur {
    position: absolute;
    left: 0;
    top: 89px;
    z-index: -2;
    filter: blur(125px);
    transform: rotate(15.16deg);
    background: #f9fff9;
    width: 300px;
    height: 400px;
  }

  &__text {
    max-width: 549px;
    position: relative;
    z-index: 3;

    &_title {
      color: $color-brown;
      font-weight: 500;
      font-size: 50px;
      line-height: 60px;
    }

    &_paragraph {
      color: $color-brown;
      font-weight: 300;
      font-size: 18px;
      line-height: 28px;
      margin-top: 30px;
    }

    .btn-green-text {
      margin-top: 30px;
    }

    &_bottom-wrapper {
      margin-top: 85px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .item {
        max-width: 153px;
        margin-right: 32px;

        .text {
          color: $color-brown;
          font-weight: 400;
          font-size: 18px;
          line-height: 28px;
          margin-top: 16px;
        }
      }

      .item:last-of-type {
        margin: 0;
      }
    }
  }

  &__calculator {
    min-width: 416px;
    height: 565px;
    background: #ffffff;
    box-shadow: 0px 0px 19px rgba(0, 0, 0, 0.1);
    border-radius: 15px;
    padding: 40px 30px;
    color: $color-brown;
    position: relative;
    z-index: 3;

    &_block {
      display: flex;
      align-items: center;
      justify-content: space-between;
      background: rgba($color: #e7e7e7, $alpha: 0.5);
      border-radius: 10px;
      padding: 20px 20px 20px 25px;

      .left {
        .head {
          font-size: 14px;
          line-height: 24px;
          font-weight: 400;
        }

        .number {
          font-size: 18px;
          line-height: 28px;
          font-weight: 500;
        }
      }

      .right {
        .currency {
          position: relative;
          border-radius: 5px;
          width: 140px;

          background: #ffffff;

          &__items {
            display: none;
            list-style: none;
            position: absolute;
            bottom: 67px;
            left: 0;
            width: 100%;
          }

          &__items_active {
            opacity: 1;
            display: block;
          }

          &__item {
            opacity: 1;
            height: auto;
            background-color: #fff;
            border-radius: 10px;
            cursor: pointer;
            font-size: 18px;
            line-height: 28px;
            color: $color-brown;
            font-weight: 500;
            padding: 10px 15px;
            display: flex;
            align-items: center;

            span {
              margin-left: 15px;
            }
          }

          &__item_active {
            background-color: transparent;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 18px 15px;
          }
        }
      }
    }

    &_middle {
      padding: 8px 20px 0 20px;
      margin: 0 0 28px;
      .item {
        display: flex;
        align-items: center;
        font-size: 15px;
        line-height: 24px;
        font-weight: 300;
        color: $color-brown;
        margin-top: 16px;

        .item-left {
          display: flex;
          align-items: center;
          width: 130px;

          .number {
            font-weight: 400;
            width: 100px;
            margin-left: 10px;
          }
        }

        .item-right {
          margin-left: 16px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 100%;
        }
      }
    }

    .button {
      border-radius: 10px;
      background: $color-brown;
      width: 100%;
      padding: 20px 0;
      font-size: 16px;
      line-height: 19px;
      font-weight: 600;
      color: #fff;
      cursor: pointer;
      border: none;
      outline: none;
      margin-top: 30px;
    }
  }
}

body.dark .main {
  .filter-blur {
    transform: rotate(15.16deg);
    background: #373d38;
  }

  &__text {
    &_title {
      color: #ffffff;
    }

    &_paragraph {
      color: #ffffff;
    }

    &_bottom-wrapper {
      .item {
        .text {
          color: #e3f3e5;
        }
      }
    }
  }
}
