.translate {
  margin-top: 100px;
  transition: 0.3s;

  .title {
    text-align: center;
  }

  .subtitle {
    margin-top: 20px;
    color: $color-brown;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
  }

  &__main {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: 60px;
    position: relative;

    .line {
      border-top: 1px dashed #d5dfd6;
      width: 81%;
      position: absolute;
      left: 0;
      top: 100px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .item {
        background: #ffffff;
        border: 2px solid $color-green;
        width: 15px;
        height: 15px;
        border-radius: 50%;
        display: block;
        margin-top: -8px;
      }

      .item:last-of-type {
        background: $color-green;
      }
    }

    &_item {
      .item-top {
        display: flex;
        align-items: center;

        .number {
          font-size: 72px;
          line-height: 20px;
          font-weight: 300;
          color: $color-green;
        }

        .text {
          font-size: 20px;
          line-height: 24px;
          font-weight: 400;
          color: $color-brown;
          margin-left: 20px;
        }
      }

      .item-paragraph {
        margin-top: 80px;
        font-size: 15px;
        line-height: 23px;
        font-weight: 400;
        color: #7d7d7d;
        max-width: 255px;
      }
    }
  }
}

body.dark .translate {
  .subtitle {
    color: #fff;
  }

  &__main {
    .line {
      .item {
        background: #232323;
      }
    }

    &_item {
      .item-top {
        .text {
          color: #fff;
        }
      }
    }
  }
}
