.advantages {
  margin-top: 220px;
  transition: 0.3s;

  &__top-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .item {
      text-align: center;
      border-right: 1px solid rgba($color: #c1c1c1, $alpha: 0.5);
      width: 319px;

      .item-number {
        font-size: 50px;
        line-height: 60px;
        font-weight: 500;
        color: $color-green;
      }

      .item-text {
        font-size: 18px;
        line-height: 28px;
        font-weight: 400;
        color: $color-brown;
        margin-top: 6px;
      }
    }

    .item:last-of-type {
      border: none;
    }
  }

  &__middle-content {
    margin: 110px auto 80px;
    text-align: center;
    max-width: 553px;

    .paragraph {
      color: $color-brown;
      font-size: 16px;
      line-height: 24px;
      font-weight: 400;
    }
  }

  &__bottom-wrapper {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    .item {
      width: 255px;
      text-align: center;

      .item-title {
        color: $color-brown;
        font-size: 18px;
        line-height: 24px;
        font-weight: 400;
        margin: 30px 0 20px;
      }

      .item-paragraph {
        color: $color-brown;
        font-size: 15px;
        line-height: 20px;
        font-weight: 300;
      }
    }
  }
}

body.dark .advantages {
  &__top-wrapper {
    .item {
      .item-text {
        color: #dddddd;
      }
    }
  }

  &__middle-content {
    .paragraph {
      color: #ffffff;
    }
  }

  &__bottom-wrapper {
    .item {
      .icon {
        svg {
          .path {
            fill: #fff;
            stroke: #fff;
          }

          .stroke {
            stroke: #fff;
          }
        }
      }

      .item-title {
        color: #fff;
      }

      .item-paragraph {
        color: #7e7e7e;
      }
    }
  }
}
