.geography {
  margin-top: 110px;
  transition: 0.3s;

  &__wrapper {
    background: #ffffff;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.08);
    border-radius: 15px;
    padding: 60px;

    &_top {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;

      .info {
        .info-title {
          color: $color-brown;
          font-size: 32px;
          line-height: 38px;
          font-weight: 400;
        }

        .info-paragraph {
          margin-top: 18px;
          color: $color-brown;
          font-size: 16px;
          line-height: 24px;
          font-weight: 400;
        }
      }
    }

    &_main {
      margin-top: 50px;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      gap: 30px 160px;
      align-items: flex-start;
      padding-right: 140px;

      .item {
        display: flex;
        align-items: center;

        .country {
          color: $color-brown;
          font-size: 16px;
          line-height: 19px;
          font-weight: 400;
          margin-left: 16px;
        }
      }
    }
  }
}

body.dark .geography {
  &__wrapper {
    background: #292929;
    box-shadow: none;

    &_top {
      .info {
        .info-title {
          color: #fff;
        }

        .info-paragraph {
          color: #7d7d7d;
        }
      }

      .button-position {
        .btn-green-border {
          color: #fff;
        }
      }
    }

    &_main {
      .item {
        .country {
          color: #fff;
        }
      }
    }
  }
}
