.contacts {
  background: #ffffff;
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.02);
  margin-top: 60px;
  padding: 30px 0;
  transition: 0.3s;

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &_nav {
      .item {
        margin-left: 50px;
        font-size: 16px;
        line-height: 19px;
        font-weight: 400;
        color: $color-brown;
      }
    }

    &_links {
      display: flex;
      align-items: center;
      margin-left: 60px;

      .item {
        background: $color-brown;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 16px;
      }
    }
  }
}

body.dark .contacts {
  background: #1a1a1a;

  &__wrapper {
    &_logo {
      a {
        svg {
          .path {
            fill: #ffffff;
          }
        }
      }
    }

    &_nav {
      .item {
        color: #ffffff;
      }
    }

    &_links {
      .item {
        background: #2a2a2a;
      }
    }
  }
}
