.footer {
  background: #f8f8f8;
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.02);
  padding: 30px 0;
  transition: 0.3s;

  &__wrapper-top {
    display: flex;

    .item {
      margin-right: 30px;
    }
  }

  &__main {
    padding: 30px 0;
    border-bottom: 1px solid rgba($color: #000000, $alpha: 0.1);

    &_text {
      font-size: 14px;
      line-height: 24px;
      font-weight: 400;
      color: $color-brown;
    }
  }

  &__bottom {
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    span {
      font-size: 14px;
      line-height: 24px;
      font-weight: 400;
      color: $color-brown;
    }

    a {
      font-size: 14px;
      line-height: 24px;
      font-weight: 400;
      color: $color-brown;
    }
  }
}

body.dark .footer {
  background: #232323;

  &__main {
    border-bottom: 1px solid rgba($color: #4e4e4e, $alpha: 0.5);

    &_text {
      color: #e7e7e7;
    }
  }

  &__bottom {
    span {
      color: #e7e7e7;
    }

    a {
      color: #e7e7e7;
    }
  }
}
