.how-it-works {
  margin-top: 100px;
  background: #f8f8f8;
  display: flex;
  transition: 0.3s;

  &__left {
    position: relative;
    .play-back {
      img {
        display: block;
      }
    }

    .play {
      background: rgba($color: #fff, $alpha: 0.4);
      width: 109px;
      height: 109px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      position: absolute;
      left: 47%;
      top: 50%;
      transform: translate(-50%, -50%);

      .triangle-wrapper {
        background: #ffffff;
        width: 89px;
        height: 89px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }

  &__main {
    margin: 123px 0 0 93px;

    .subtitle {
      font-size: 18px;
      line-height: 24px;
      font-weight: 400;
      margin: 20px 0 13px;
      color: $color-brown;
    }

    .item {
      display: flex;
      align-items: center;
      margin-top: 20px;

      .text {
        font-size: 16px;
        line-height: 28px;
        font-weight: 400;
        color: $color-brown;
        margin-left: 16px;
      }
    }

    .btn-green-text {
      margin-top: 50px;
    }
  }

  &__right {
    position: relative;
    .dot-map {
      position: absolute;
      top: 90px;
      right: 0;
    }
  }
}

body.dark .how-it-works {
  background: #292929;

  &__main {
    .subtitle {
      color: #fff;
    }

    .item {
      .text {
        color: #fff;
      }
    }
  }
}
