.faq {
  margin-top: 100px;
  transition: 0.3s;

  .title {
    text-align: center;
  }

  .subtitle {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    color: $color-brown;
    text-align: center;
    margin-top: 20px;
  }

  &__wrapper {
    position: relative;
    margin-top: 50px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    .faq-item {
      will-change: height;
      overflow-y: hidden;
      position: relative;
      padding: 23px 18px 23px 31px;
      cursor: pointer;
      background: #ffffff;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.08);
      border-radius: 15px;
      width: 620px;
      margin-top: 20px;

      .faq-top {
        position: relative;
        margin: 0 0 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .title {
          font-weight: 400;
          font-size: 16px;
          color: $color-brown;
          line-height: 24px;
        }
      }

      .answer {
        width: 100%;
        font-weight: 400;
        font-size: 16px;
        color: $color-brown;
        line-height: 24px;
      }
    }

    .faq-item.open {
      .close {
        transform: rotate(180deg);
      }
    }
  }

  .button-position {
    text-align: center;
    margin-top: 80px;
  }
}

body.dark .faq {
  .subtitle {
    color: #ffffff;
  }

  &__wrapper {
    .faq-item {
      background: #292929;
      box-shadow: none;

      .faq-top {
        .title {
          color: #e7e7e7;
        }
      }

      .answer {
        color: #e7e7e7;
      }
    }
  }

  .button-position {
    text-align: center;
    margin-top: 80px;

    .btn-green-border {
      color: #ffffff;
    }
  }
}
